*{
    margin: 0;
    padding: 0;
}
.header{
    width: 100%;
    height: 60px;
    background-color: black;
    margin: 0 auto;display: flex;
    position: fixed;
    z-index: 100;
}
.header__hat{
    width: 100%;
    height: 2px;
    color:#333;
    position: absolute;
    background: #333;
}
.header__left{
    display: flex;
}
.header__disk{
    width: 58px;
    height: 58px;
    padding-top: 2px;
}
.header__btns{
    display: flex;
    color: #333;
    align-items: center;
    padding-left: 60px;
}
.header__btn{
    font-size: 18px;
}
.header__btn2{
    font-size: 33px;
    padding: 0 20px;
}
.header__logo{
    cursor: pointer;
}
.header__logo-link{
    padding-top: 16px;
    padding-left: calc(50% - 279px);
}
.header__right{
    display: flex;
    padding-right: 10px;
    margin-left: auto;
}
.header__menu{
    display: flex;
    color: #C0C0C0;
    font-size: 15px;
    font-family: 'Inconsolata', monospace;
    align-items: center;
    line-height: 40px;
}
.header__search{
    cursor: pointer;
    height: 25px;
}
.header__search:hover{
    color: #eee;
}
.header__menu__item{
    margin-left: 20px;
    cursor: pointer;
}
.header__menu__item:hover{
    color: #eee;
}
.header__right-menu{
    display: flex;
    align-items: center;
    font-family: 'Inconsolata', monospace;
    padding-left: 15px;
    line-height: 40px;
}
.header__right-menu__dot{
    color: #C0C0C0;
    font-size: 3px;
}
.header__right-menu__1link{
    color: #C0C0C0;
    cursor: pointer;
    padding: 0 20px;
    font-size: 14px;
}
.header__right-menu__1link:hover{
    color: #eee;
}
.header__right-menu__2link{
    color: #222;
    background-color: #C0C0C0;
    border-radius: 3px;
    padding: 0 20px;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
}
.header__right-menu__2link:hover{
    background-color: #eee;
}
.header__adapt__sp__two{
    display: none;
}
.head__tel__adapt__spec{
    display: none;
}
@media screen and (min-width: 1024px) and (max-width: 1509px){
    .header__adapt__sp{
        display: none;
    }
    .header__adapt__sp__two{
        display: block;
    }
}
@media screen and (min-width: 420px) and (max-width: 1023px){
    .head__tel__adapt{
        display: none;
    }
    .head__tel__adapt__spec{
        display: block;
        width: 24px;
        height: 24px;
        padding: 16px 0 0 15px;
    }
    .header__search{
        font-size: 25px;
        margin-top: 10px;
    }
    .header__logo-link{
        padding-top: 16px;
        padding-left: calc(50% - 90px);
    }
}